body,
html {
  height: 100%;
  overflow-x: hidden;
  background-color: #f7f7f7;
}
rs-hero > * {
  user-select: none;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}
.content > * {
  user-select: none;
}
.detail-content {
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.detail-content > * {
  user-select: none;
}
.card {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: stretch;
  border: 1px solid #d7d6d6;
  margin-bottom: 1em;
  padding: 0.5em;
}
.card:last-child {
  margin-bottom: 0;
}
.wpm-box {
  display: flex;
  align-items: baseline;
}
.wpm-box p {
  margin-left: 1em;
}
.slider-box {
  display: flex;
  align-items: center;
}
#wpmSlider {
  margin: 0 1em;
}
.legend {
  height: 100%;
  display: flex;
  align-self: stretch;
  justify-content: space-around;
  padding: 2em;
  flex-wrap: wrap;
}
.legend__step {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.legend__step__icon {
  margin-top: 1em;
  width: 30px;
  height: 30px;
}
@media (max-width: 230px) {
  .content {
    height: 100%;
    box-sizing: border-box;
  }
  .hero {
    display: none;
  }
  .hero-sticky-header {
    display: none;
  }
  .card-slider {
    display: none;
  }
  .legend {
    padding: 0;
  }
  .legend__step {
    min-width: 50px;
    margin-bottom: 0.5em;
  }
}
@media (max-height: 230px) {
  body {
    overflow-y: hidden;
  }
}
